.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 32px;
  text-transform: uppercase;
}

.site-list__item a {
  display: block;
  height: 50px;
  padding-top: 15px;
  text-decoration: none;
  color: @colorwhite;
  text-align: center;
  box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);

  &:hover,
  &:focus {
    color: @colorformtitle;
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: @desktop-width) {
  .site-list__item a {
    box-shadow: none;
  }

  .site-list {
    font-weight: normal;
    font-size: 18px;
    text-transform: none;
    line-height: 18px;
    width: 622px;
    height: 44px;
    margin-left: 20px;
    display: flex;
    justify-content: space-between;
  }
}

.site-list__item--active {
  @media (min-width: @desktop-width) {
    border-bottom: 2px solid @colorwhite;
  }
}
