.main-nav__toggle {
  position: absolute;
  top: 10px;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 45px;
  background-color: inherit;
  cursor: pointer;
  border: none;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 30px;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 13px;
  width: 51px;
  height: 4px;
  background-color: #FFFFFF;
  box-shadow: 0 10px 0 0 #FFFFFF, 0 19px 0 0 #FFFFFF;
}
.main-nav--opened .main-nav__toggle {
  top: 10px;
  right: 5px;
}
@media (min-width: 660px) {
  .main-nav--opened .main-nav__toggle {
    top: 25px;
  }
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 25px;
  width: 36px;
  height: 2px;
  background-color: #FFFFFF;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after,
.main-nav--opened .main-nav__toggle:focus::after,
.main-nav--opened .main-nav__toggle:focus::before {
  background-color: #D22856;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: #D22856;
  opacity: 0.3;
}
.main-nav--closed .main-nav__toggle:hover::before,
.main-nav--closed .main-nav__toggle:focus::before {
  background-color: #D22856;
  box-shadow: 0 10px 0 0 #D22856, 0 19px 0 0 #D22856;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: #D22856;
  box-shadow: 0 10px 0 0 #D22856, 0 19px 0 0 #D22856;
  opacity: 0.3;
}
@media (max-width: 1199px) {
  .main-nav--closed .site-list__item {
    display: none;
